<template>
  <div class="libgif">
    <button @click="parseImg">解析gif</button>
    <button @click="addIndex">下一帧</button>
    <button @click="playImg">播放</button>
    <button @click="stopImg">停止</button>
    <div
      class="gif-content"
      :style="gifCont"
    >
      <div
        class="gif-animation"
        :style="gifAnim"
      >
      </div>
    </div>
  </div>
</template>
<script>
import superGif from "./libgif.js";
export default {
  name: "libgif",
  computed: {
    gifCont() {
      const style = {};
      style.width = this.imgData.width + "px";
      style.height = this.imgData.height + "px";
      return style;
    },
    gifAnim() {
      const style = {};
      style.width = this.delays.length * 100 + "%";
      style.height = "100%";
      style.background =
        "url(" + this.imgData.url + ") 50% 50% / 100% 100% no-repeat";
      style.transform = `translateX(${(-100 * this.index) /
        (this.length || 1)}%)`;
      return style;
    }
  },
  data() {
    return {
      SuperGif: null,
      srcc: "",
      imgData: {
        width: 0,
        height: 0,
        left: 0,
        right: 0,
        url: ""
      },
      delays: [],
      index: 0,
      length: 0,
      fps: 10,
      animFrame: 0,
      isPlay: false
    };
  },
  mounted() {},
  methods: {
    parseImg() {
      var _gif = new Image();
      _gif.src =
        "https://resources.laihua.com/2019-3-25/9d7b53dd-ff79-4b40-af07-72f1c974863c.gif";
      _gif.onload = () => {
        this.imgData.width = _gif.width;
        this.imgData.height = _gif.height;
        var SuperGif = superGif();
        var cg = new SuperGif({
          src: _gif.src,
          width: _gif.width,
          height: _gif.height,
          auto_play: 0
        });
        cg.load(data => {
          this.imgData.url = data.blobUrl;
          this.delays = [].concat(data.delays);
          this.length = this.delays.length;
          this.get_fps();
          cg = null;
          _gif = null;
          data = null;
        });
      };
    },
    addIndex(r) {
      if (typeof r === "number") {
        this.index = r;
        return;
      }
      if (this.length) {
        if (this.index + 1 >= this.length) {
          this.index = 0;
        } else {
          this.index++;
        }
      }
    },
    playImg() {
      if (this.isPlay || this.length <= 0) return;
      // 分组播放
      let start = 0,
        step = null;

      this.isPlay = true;

      step = timestamp => {
        if (!this.isPlay) {
          window.cancelAnimationFrame(this.animFrame);
          return;
        }
        let progress = 0;
        if (!start) start = timestamp;
        progress = (timestamp - start) / 1000;
        let frame = Math.ceil(progress * 60);
        var f = Math.floor((frame / 60) * this.fps);
        var r = f % this.length;
        this.addIndex(r);
        this.animFrame = window.requestAnimationFrame(step);
      };

      this.animFrame = window.requestAnimationFrame(step);
    },
    stopImg() {
      if (this.isPlay) {
        window.cancelAnimationFrame(this.animFrame);
        this.isPlay = false;
      }
    },
    get_fps: function() {
      // 获取gif图帧率
      let duration = 0;
      this.delays.forEach(function(v) {
        duration += v;
      });

      if (!duration) return 10;
      this.fps = Math.round(this.length / (duration / 100));
    }
  }
};
</script>
<style lang="scss" scoped>
.gif-content {
  overflow: hidden;
}
</style>
